import React, { Component } from 'react';
import donateImg from "../../img/donate.jpg";
import './Donation.css';

class Donation extends Component {

    render() {
        const { username, donationAmount, message} = this.props;
        return (
            <div>
                <div id="widget" className="widget-AlertBox" data-layout="above">
                    <div id="alert-box">
                        <div id="particles" className="hidden" />
                        <div id="wrap">
                        <div id="alert-image-wrap">
                            <div id="alert-image" style={{backgroundImage: 'url("' + donateImg + '")'}}><img alt="donate" style={{height: '1px', opacity: 0, width: '1px'}} src="https://uploads.twitchalerts.com/000/047/281/189/250208.gif" /></div>
                        </div>
                        <div id="alert-text-wrap">
                            <div id="alert-text">
                            <div id="alert-message" style={{fontSize: '36px', color: '#d48e26', fontFamily: '"Josefin Sans"', fontWeight: 800}}>
                                {"非常感謝 "}
                                <span data-token="name" style={{color: '#32c3a6', position: 'relative'}}><span><span className="animated-letter">
                                    {username}
                                </span></span></span>
                                {" 贊助 "}
                                <span data-token="amount" style={{color: '#32c3a6', position: 'relative'}}><span><span className="animated-letter">
                                    {donationAmount}
                                </span></span></span>
                                {" 元!!"}
                                </div>
                                <div id="alert-user-message" style={{fontWeight: 700, fontSize: '40px', color: '#d0d426', fontFamily: '"Josefin Sans"'}}>
                                    {message}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }

}

export default Donation;