import React, {Component} from "react";
import parse from "html-react-parser";
import "./Sub.css";
import subFastImg from "../../img/sub/subfast.gif";

class Sub extends Component {
  render() {
    const {username, message, months, subType} = this.props;
    return (
      <div>
        <div id="widget" className="widget-AlertBox" data-layout="above">
          <div id="alert-box">
            <div id="particles" />
            <div
              id="wrap"
              style={{
                width: "800px",
                height: "308px",
                paddingTop: "30px"
              }}
            >
              <div id="alert-image-wrap">
                <div
                  id="alert-image"
                  style={{backgroundImage: 'url("' + subFastImg + '")'}}
                >
                  <img
                    alt="sub"
                    style={{height: "1px", opacity: 0, width: "1px"}}
                    src={subFastImg}
                  />
                </div>
              </div>
              <div id="alert-text-wrap">
                <div
                  id="alert-text"
                  style={{
                    fontSize: 32,
                    color: "rgb(255, 255, 255)",
                    fontFamily: '"Yu Mincho"',
                    fontWeight: 600,
                  }}
                >
                  Lv. {months + " "}
                  <span
                    data-token="name"
                    style={{color: "rgb(120, 232, 182)", position: "relative"}}
                  >
                    {username}
                  </span>{" "}
                  Thank you！Keep on keeping on！
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Sub;
