import React, {Component} from "react";
import parse from "html-react-parser";
import "./Sub.css";
import subImg from "../../img/sub/sub.gif";
import subImg2 from "../../img/sub/sub2.gif";

class Sub extends Component {
  render() {
    const {username, message, months, refresh} = this.props;
    return (
      <div>
        <div id="widget" className="widget-AlertBox" data-layout="banner">
          {/* main alert box window */}
          <div id="alert-box">
            {/* particles */}
            <div id="particles" />
            {/* wrap window */}
            <div
              id="wrap"
              style={{
                width: "708px",
                height: "200px",
              }}
            >
              {/* alert image */}
              <div id="alert-image-wrap">
                <div
                  id="alert-image"
                  style={{
                    backgroundImage:
                      "url(" + (refresh ? subImg : subImg2) + ")",
                  }}
                >
                  <img
                    alt="sub"
                    style={{height: "1px", opacity: 0, width: "1px"}}
                    src={refresh ? subImg : subImg2}
                  />
                </div>
              </div>
              {/* main alert box window */}
              <div id="alert-text-wrap">
                {/* alert text */}
                <div id="alert-text">
                  {/* alert message */}
                  <div
                    id="alert-message"
                    style={{
                      fontSize: "36px",
                      color: "rgb(255, 255, 255)",
                      fontFamily: '"Yu Mincho"',
                      fontWeight: 800,
                    }}
                  >
                    <b
                      style={{
                        fontFamily: "Yu Mincho",
                        margin: "0 auto",
                        marginLeft: "50px",
                        lineHeight: "200px",
                        textAlign: "left",
                      }}
                    >
                      Lv.<span data-token="months">{months}</span>　
                      <span data-token="name">{username}</span>
                    </b>
                  </div>
                  {/* <div
                    id="alert-user-message"
                    style={{
                      fontWeight: 100,
                      fontSize: "24px",
                      color: "rgb(255, 255, 255)",
                      fontFamily: '"Yu Mincho"',
                    }}
                  >
                    {parse(message)}
                  </div> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Sub;
