import React, {Component} from "react";
import parse from "html-react-parser";
import cheerImg from "../../img/bit/cheer.gif";
import "./Cheer.css";

class Cheer extends Component {
  render() {
    const {username, bits, message} = this.props;
    // const imgList = require(`../../img/bit/bit (${cheerImg}).gif`)
    return (
      <div>
        <div id="widget" className="widget-AlertBox" data-layout="side">
          <div id="alert-box">
            <div id="particles" className="hidden" />
            <div id="wrap">
              {/* alert image */}
              <div id="alert-image-wrap">
                <div
                  id="alert-image"
                  style={{backgroundImage: 'url("' + cheerImg + '")'}}
                >
                  <img
                    alt="cheer"
                    style={{height: "1px", opacity: 0, width: "1px"}}
                    src={cheerImg}
                  />
                </div>
              </div>
              {/* main alert box window */}
              <div id="alert-text-wrap">
                {/* alert text */}
                <div id="alert-text">
                  {/* alert message */}
                  <div id="alert-message">
                    <span
                      data-token="name"
                      style={{
                        color: "rgb(50, 195, 166)",
                        position: "relative",
                      }}
                    >
                      <span
                        className="animated-letter pulse"
                        style={{
                          fontSize: 22,
                          color: "rgb(50, 195, 166)",
                          fontFamily: '"Yu Mincho"',
                          fontWeight: 600,
                        }}
                      >
                        {username}
                      </span>
                    </span>
                    {" cheered! x "}
                    <span
                      data-token="amount"
                      style={{
                        color: "rgb(50, 195, 166)",
                        position: "relative",
                      }}
                    >
                      <span className="animated-letter pulse">{bits}</span>
                    </span>
                  </div>
                  <div
                    id="alert-user-message"
                    style={{
                      fontWeight: 400,
                      fontSize: 20,
                      color: "rgb(255, 255, 255)",
                      fontFamily: '"Yu Mincho"',
                    }}
                  >
                    {parse(message)}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Cheer;
