import React, { Component } from 'react';
import raidImg from "../../img/raid.gif";
import './Raid.css';

class Raid extends Component {

    render() {
        const { username, members} = this.props;
        return (
            <div>
                <div id="widget" className="widget-AlertBox" data-layout="above">
                    <div id="alert-box">
                        <div id="particles" className="hidden" />
                        <div id="wrap">
                        <div id="alert-image-wrap">
                            <div id="alert-image" style={{backgroundImage: 'url("' + raidImg + '")'}}><img alt="donate" style={{height: '1px', opacity: 0, width: '1px'}} src="https://uploads.twitchalerts.com/000/047/281/189/250208.gif" /></div>
                        </div>
                        <div id="alert-text-wrap">
                            <div id="alert-text">
                            <div id="alert-message" style={{fontSize: '36px', color: 'rgb(255, 255, 255)', fontFamily: '"Josefin Sans"', fontWeight: 800}}>
                                <span data-token="name" style={{color: "rgb(50, 195, 166)", position: 'relative'}}><span><span className="animated-letter">
                                    {username}
                                </span></span></span>
                                {" is raiding with a party of "}
                                <span data-token="amount" style={{color: "rgb(50, 195, 166)", position: 'relative'}}><span><span className="animated-letter">
                                    {members}
                                </span></span></span>
                                {"!"} 
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }

}

export default Raid;